import { Editor } from '@tinymce/tinymce-react';
import PropTypes from 'prop-types';

function TextEditorRender({ textValue, loadBody, setTextEditor }) {
    return (
        <Editor
            value={textValue}
            onInit={(_evt, editor) => loadBody(editor)}
            onEditorChange={(value) => {
                setTextEditor(value);
            }}
            apiKey={process.env.REACT_APP_TINY_MCE_API_KEY}
            init={{
                height: 500,
                plugins:
                    'print preview paste importcss searchreplace autolink autosave save directionality code visualblocks visualchars ' +
                    'fullscreen image link media template codesample table charmap hr pagebreak nonbreaking anchor toc insertdatetime ' +
                    'advlist lists wordcount imagetools textpattern noneditable help charmap quickbars emoticons visualblocks',
                imagetools_cors_hosts: ['picsum.photos'],
                toolbar:
                    'bold italic underline strikethrough | fontselect fontsizeselect forecolor formatselect | alignleft aligncenter alignright alignjustify lineheight| ' +
                    'outdent indent | numlist bullist | insertfile image media link | fullscreen  preview print |emoticons',
                fontsize_formats: '8pt 10pt 11pt 12pt 14pt 18pt 24pt',
                block_formats: 'Bloco=div;Paragrafo=p;Header 1=h1;Header 2=h2;Header 3=h3;Preformatted=pre',
                content_style: 'body { font-family:Arial,Helvetica,sans-serif; font-size:14pt }',
                image_caption: true,
                image_advtab: true,
                contextmenu: 'link image imagetools table configurepermanentpen',
                a11y_advanced_options: true,
                convert_urls: true,
                preview_styles: false,
                textcolor_rows: '4',
                line_height_formats: '1 1.1 1.2 1.5'
            }}
        />
    );
}

TextEditorRender.propTypes = {
    textValue: PropTypes.string,
    loadBody: PropTypes.func,
    setTextEditor: PropTypes.func
};

export default TextEditorRender;
