import apiService from './apiService';
import { setListCampaign } from 'store/reducers/campaignReducer';

const basePath = '/api/v1/campaign';

/**
 * Fetch to campaign data
 * @async
 * @function
 * @return {Promise<JSON>} Object campaign data
 */
export function fetchCampaignList({ page = 1, pageSize = 50, useCache = false }) {
    return async function fetchCampaignThunk(dispatch, getState) {
        const moreFilter = `&useCache=${useCache}`;
        try {
            const url = `${basePath}/filter?pageSize=${pageSize}&page=${page}${moreFilter}`;
            const response = await apiService(getState()).get(url);
            await dispatch(setListCampaign(response));
            return response;
        } catch (ex) {
            console.error('Error Fetch Campaign', ex.message);
            return null;
        }
    };
}

export function postCampaign(data) {
    return async function postCampaignThunk(_dispatch, getState) {
        try {
            await apiService(getState())
                .post(basePath, data)
                .then((response) => response?.data);
            return true;
        } catch (ex) {
            console.error('Error Post Campaign: ', ex.message);
            throw ex;
        }
    };
}
