export default async function loadImages(multimedias) {
    const images = [];
    if (multimedias != null) {
        await multimedias.map(async (item) => {
            try {
                const photo = await fetch(item.linkMedia);
                if (photo.ok) {
                    const photoBlob = await photo.blob();
                    const reader = new FileReader();
                    reader.onloadend = () => {
                        images.push({ dataUrl: reader.result });
                    };
                    reader.readAsDataURL(photoBlob);
                }
            } catch (e) {
                console.error(`Error download image: ${e.message} - ${item.linkMedia}`);
            }
            return images;
        });
    }
    return images;
}

export async function loadAsyncImages(multimedias, setImageFunc) {
    if (multimedias != null) {
        await multimedias.map(async (item) => {
            try {
                const photo = await fetch(item.linkMedia);
                if (photo.ok) {
                    const photoBlob = await photo.blob();
                    const reader = new FileReader();
                    reader.onloadend = () => {
                        if (reader.result != null) {
                            setImageFunc({ dataUrl: reader.result });
                        }
                    };
                    reader.readAsDataURL(photoBlob);
                }
            } catch (e) {
                console.error(`Error download async image: ${e.message} - ${item.linkMedia}`);
            }
        });
    }
}
