import { Avatar, Button, Divider, Grid, InputLabel, Stack, useMediaQuery } from '@mui/material';
import AnimateButton from '../extended/AnimateButton';
import ImageUploading from 'react-images-uploading';
import { useTheme } from '@mui/material/styles';
import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import IconButton from '@mui/material/IconButton';
import { green, red } from '@mui/material/colors';
import DeleteForever from '@mui/icons-material/DeleteForever';
import Cached from '@mui/icons-material/Cached';

function ImagePanelUpload({
    images,
    onChange,
    maxNumber = 8,
    multiple = true,
    title = null,
    titleButton = null,
    renderAvatar = false,
    altImage = null,
    positionButtonHeader = true
}) {
    const theme = useTheme();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
    const [currentImages, setCurrentImages] = useState([]);
    const [labelTitle, setLabelTitle] = useState(null);
    const [labelButton, setLabelButton] = useState(null);

    useEffect(() => {
        setLabelTitle(title);
        setLabelButton(titleButton);
        setCurrentImages(images);
        return () => setCurrentImages([]);
    }, [images]);

    function onChangeImages(imageList) {
        setCurrentImages(imageList);
        onChange(imageList);
    }

    function getTitle() {
        if (labelTitle == null) {
            return (
                <span>
                    Somente imagens no formato <strong>.jpg</strong>
                </span>
            );
        }
        return <span>{labelTitle}</span>;
    }
    function getTitleButton() {
        if (labelButton == null) {
            return <>Importar</>;
        }
        return <>{labelButton}</>;
    }

    function renderButtonsFooterImage(index, onImageUpdate, onImageRemove) {
        return (
            <div className="image-item__btn-wrapper">
                <IconButton sx={{ color: green[500] }} onClick={() => onImageUpdate(index)} aria-label="Atualizar">
                    <Cached sx={{ fontSize: 26 }} />
                </IconButton>
                <IconButton sx={{ color: red[500] }} onClick={() => onImageRemove(index)} aria-label="Remover">
                    <DeleteForever sx={{ fontSize: 26 }} />
                </IconButton>
            </div>
        );
    }

    function renderContentImage(imageList, onImageUpdate, onImageRemove) {
        if (renderAvatar && !multiple) {
            return (
                <Grid container justifyContent="center" alignItems="center" textAlign="center" sx={{ mb: 4 }}>
                    <Grid item xs={12} sm={12}>
                        <div style={{ margin: 'auto', border: 1, borderWidth: 1 }}>
                            {imageList !== null && imageList.length > 0 ? (
                                <>
                                    <Avatar alt={altImage} src={imageList[0].dataUrl} sx={{ width: 100, height: 100, margin: 'auto' }} />
                                    {renderButtonsFooterImage(0, onImageUpdate, onImageRemove)}
                                </>
                            ) : (
                                <Avatar alt={altImage} src={null} sx={{ width: 100, height: 100, margin: 'auto' }} />
                            )}
                        </div>
                    </Grid>
                </Grid>
            );
        }
        return (
            <Grid container direction="row" marginTop={2}>
                {imageList !== null &&
                    imageList?.map((image, index) => (
                        <Grid key={`iamge_item_${index}`} item xs={4} sm={4} textAlign="center">
                            <img src={image.dataUrl} alt="" width="300" height="180" />
                            {renderButtonsFooterImage(index, onImageUpdate, onImageRemove)}
                        </Grid>
                    ))}
            </Grid>
        );
    }

    function renderButtonAndTitle(onImageUpload, onImageRemoveAll, dragProps, imageList) {
        if (renderAvatar && imageList.length > 0) {
            return null;
        }
        return (
            <>
                <Grid container alignItems="center" justifyContent="center" spacing={matchDownSM ? 0 : 2}>
                    <Grid
                        item
                        textAlign="center"
                        alignItems="center"
                        justifyContent="center"
                        xs={12}
                        sm={12}
                        marginBottom={1}
                        style={{ paddingTop: 0 }}
                    >
                        <span>{getTitle()}</span>
                    </Grid>
                </Grid>
                <Grid container alignItems="center" justifyContent="center" spacing={matchDownSM ? 0 : 2}>
                    <Grid item xs={12} sm={renderAvatar && !multiple ? 6 : 2}>
                        <AnimateButton>
                            <Button
                                fullWidth
                                disabled={currentImages.length === maxNumber}
                                disableElevation
                                size="small"
                                variant="contained"
                                color="primary"
                                onClick={onImageUpload}
                                {...dragProps}
                            >
                                {getTitleButton()}
                            </Button>
                        </AnimateButton>
                    </Grid>
                    {multiple && (
                        <Grid item xs={12} sm={2}>
                            <AnimateButton>
                                <Button
                                    fullWidth
                                    disableElevation
                                    size="small"
                                    variant="contained"
                                    color="secondary"
                                    onClick={onImageRemoveAll}
                                >
                                    Remover todas
                                </Button>
                            </AnimateButton>
                        </Grid>
                    )}
                    <Grid item xs={12} sm={12}>
                        <Divider sx={{ flexGrow: 1 }} orientation="horizontal" />
                    </Grid>
                </Grid>
            </>
        );
    }
    return (
        <ImageUploading
            multiple={multiple}
            acceptType={['jpg']}
            value={currentImages || []}
            onChange={(items) => onChangeImages(items)}
            maxNumber={maxNumber}
            dataURLKey="dataUrl"
        >
            {({ imageList, onImageUpload, onImageRemoveAll, onImageUpdate, onImageRemove, dragProps }) => (
                <Grid>
                    {positionButtonHeader && renderButtonAndTitle(onImageUpload, onImageRemoveAll, dragProps, imageList)}
                    {renderContentImage(imageList, onImageUpdate, onImageRemove)}
                    {!positionButtonHeader && renderButtonAndTitle(onImageUpload, onImageRemoveAll, dragProps, imageList)}
                </Grid>
            )}
        </ImageUploading>
    );
}

ImagePanelUpload.propTypes = {
    images: PropTypes.arrayOf(
        PropTypes.shape({
            dataUrl: PropTypes.string
        })
    ),
    dataUrl: PropTypes.string,
    imageList: PropTypes.arrayOf(
        PropTypes.shape({
            dataURL: PropTypes.string,
            file: PropTypes.object
        })
    ),
    onChange: PropTypes.func,
    setImages: PropTypes.func,
    maxNumber: PropTypes.number,
    multiple: PropTypes.bool
};

ImagePanelUpload.defaultProps = {
    maxNumber: 8
};

export default ImagePanelUpload;
