import { useEffect, useState, useRef } from 'react';
import { Editor } from '@tinymce/tinymce-react';
import { Formik } from 'formik';
import {
    Box,
    Button,
    Checkbox,
    FormControlLabel,
    FormHelperText,
    Grid,
    TextField,
    Typography,
    useMediaQuery,
    Tab,
    FormControl,
    Divider
} from '@mui/material';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import { useLocation, useNavigate } from 'react-router-dom';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { useTheme } from '@mui/material/styles';
import useScriptRef from '../../../hooks/useScriptRef';
import { useDispatch, useSelector } from 'react-redux';
import { formatDate, ISODateFormat } from '../../../utils/utility';
import { get } from 'axios';
import PropTypes from 'prop-types';
import { loadAsyncImages } from '../../utilities/ImageUtil';
import { setMessage } from 'store/reducers/dialogReducer';
import ImagePanelUpload from '../../../ui-component/images/ImagePanelUpload';
import { postCampaign } from 'services/campaignService';
import TextEditorRender from '../../utilities/TextEditorRender';

function Manager() {
    const theme = useTheme();
    const scriptedRef = useScriptRef();
    const editorRef = useRef(null);

    const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
    const dispatch = useDispatch();
    const location = useLocation();
    const navigate = useNavigate();
    const campaign = useSelector((state) => state.campaign);
    const { items } = campaign;

    const [currentCampaign, setCurrentCampaign] = useState({});
    const [tab, setTab] = useState('1');
    const [checked, setChecked] = useState(false);
    const [images, setImages] = useState([]);
    const [textEditor, setTextEditor] = useState('');
    const [errors, setErrors] = useState(null);

    const handleChange = (_event, newValue) => {
        setTab(newValue);
    };

    function setImagesDownload(imageToSet) {
        setImages((oldValue) => oldValue.concat(imageToSet));
    }
    async function downloadImages(multimedias) {
        await loadAsyncImages(multimedias, setImagesDownload);
    }

    useEffect(() => {
        const code = location.state?.code;
        if (code != null) {
            const result = items.find((item) => item.code === code);
            if (result != null) {
                const multimedias = [{ linkMedia: result.linkImageEmphasis }];
                setChecked(result.active);
                const newState = { ...currentCampaign, ...result, multimedias };
                setCurrentCampaign(newState);
                downloadImages(multimedias).catch();
            }
        }
    }, [items]);

    function loadBody(editor) {
        editorRef.current = editor;
        if (!['', null].includes(textEditor)) {
            return;
        }
        if (currentCampaign && currentCampaign?.linkHtml && currentCampaign?.linkHtml.startsWith('http')) {
            get(currentCampaign?.linkHtml)
                .then((response) => {
                    setTextEditor(response.data);
                })
                .catch((error) => console.error(`Error download html from campaign ${error.message}`));
        }
    }

    function setValue(key, value) {
        setCurrentCampaign({ ...currentCampaign, [key]: value });
    }

    async function handleValidate() {
        const keysValidate = ['title', 'startDate', 'resume'];
        setErrors(null);
        const error = {};
        let result = true;
        // eslint-disable-next-line array-callback-return
        keysValidate.map((key) => {
            if (['', null].includes(currentCampaign[key])) {
                error[key] = 'Preenchimento obrigatrio';
                setErrors({ ...error });
                dispatch(setMessage({ message: 'Os campos marcados com ( * ) so de preenchimento obrigatrios' }));
                result = false;
            }
        });
        if (result === false) {
            return false;
        }

        if (images.length === 0) {
            result = false;
            dispatch(setMessage({ message: ' obrigatrio adicionar uma imagem!' }));
            return result;
        }
        if (textEditor.replace(/(<([^>]+)>)/g, '').trim().length === 0) {
            result = false;
            dispatch(setMessage({ message: ' obrigatrio adicionar uma descrio para a campanha!' }));
            return result;
        }

        return true;
    }

    const handlePostData = async () => {
        let postData = currentCampaign;
        if (editorRef.current) {
            postData = { ...postData, linkHtml: editorRef.current.getContent() };
        }
        const imagesPost = [];
        images.map((item) => imagesPost.push(item.dataUrl));
        postData = {
            ...postData,
            active: checked,
            images: imagesPost
        };
        const code = location.state?.code;
        try {
            await dispatch(postCampaign(postData));
            const action = [{ title: 'Ok', actionButton: navigate('/campaign'), closeDialog: true }];
            dispatch(setMessage({ message: `Registro ${code ? 'editado' : 'inserido'} com sucesso!`, buttons: action }));
        } catch (ex) {
            dispatch(setMessage({ message: `Erro ao ${code ? 'editar' : 'inserir'} o registro` }));
            console.error('Error Post Campaign', ex.message);
        }
    };

    function renderImageUpload() {
        return <ImagePanelUpload images={images} onChange={(list) => setImages(list)} maxNumber={1} multiple={false} />;
    }

    function renderForm() {
        return (
            <>
                <Formik
                    initialValues={{ currentCampaign }}
                    onSubmit={async (_values, { _, setStatus, setSubmitting }) => {
                        try {
                            if (scriptedRef.current) {
                                if (await handleValidate()) {
                                    await handlePostData();
                                    setStatus({ success: true });
                                    setSubmitting(false);
                                }
                            }
                        } catch (err) {
                            if (scriptedRef.current) {
                                setStatus({ success: false });
                                setSubmitting(false);
                                dispatch(setMessage({ message: err.message }));
                            }
                        }
                    }}
                >
                    {({ handleSubmit, isSubmitting }) => (
                        <form noValidate onSubmit={handleSubmit}>
                            <Grid container alignItems="center">
                                <Grid item xs={12} sm={2}>
                                    <FormControlLabel
                                        control={
                                            <Checkbox
                                                checked={checked}
                                                onChange={(event) => setChecked(event.target.checked)}
                                                name="checked"
                                                color="primary"
                                            />
                                        }
                                        label={<Typography variant="subtitle1">Ativa</Typography>}
                                    />
                                </Grid>
                            </Grid>
                            {/* ========================================= */}
                            <Grid container spacing={matchDownSM ? 0 : 2}>
                                <Grid item xs={12} sm={6}>
                                    <FormControl fullWidth error={Boolean(errors && errors.title)} sx={{ ...theme.typography.customInput }}>
                                        <TextField
                                            fullWidth
                                            required
                                            inputProps={{ maxLength: 30 }}
                                            label="Ttulo"
                                            margin="normal"
                                            name="title"
                                            type="text"
                                            value={currentCampaign?.title}
                                            onChange={(e) => setValue('title', e.target.value)}
                                            sx={{ ...theme.typography.customInput }}
                                        />
                                        {errors && errors.title && (
                                            <FormHelperText error id="standard-weight-helper-text-email-login">
                                                {errors.title}
                                            </FormHelperText>
                                        )}
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} sm={3}>
                                    <FormControl
                                        fullWidth
                                        error={Boolean(errors && errors.startDate)}
                                        sx={{ ...theme.typography.customInput }}
                                    >
                                        <TextField
                                            fullWidth
                                            required
                                            label="Incio"
                                            margin="normal"
                                            name="startDate"
                                            type="date"
                                            value={formatDate(ISODateFormat(currentCampaign?.startDate))}
                                            onChange={(e) => setValue('startDate', e.target.value)}
                                            sx={{ ...theme.typography.customInput }}
                                        />
                                        {errors && errors.startDate && (
                                            <FormHelperText error id="standard-weight-helper-text-email-login">
                                                {errors.startDate}
                                            </FormHelperText>
                                        )}
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} sm={3}>
                                    <FormControl
                                        fullWidth
                                        error={Boolean(errors && errors.endDate)}
                                        sx={{ ...theme.typography.customInput }}
                                    >
                                        <TextField
                                            fullWidth
                                            required
                                            label="Fim"
                                            margin="normal"
                                            name="endDate"
                                            type="date"
                                            value={formatDate(ISODateFormat(currentCampaign?.endDate))}
                                            onChange={(e) => setValue('endDate', e.target.value)}
                                            sx={{ ...theme.typography.customInput }}
                                        />
                                        {errors && errors.endDate && (
                                            <FormHelperText error id="standard-weight-helper-text-email-login">
                                                {errors.endDate}
                                            </FormHelperText>
                                        )}
                                    </FormControl>
                                </Grid>
                            </Grid>
                            <Grid container spacing={matchDownSM ? 0 : 2}>
                                <Grid item xs={12} sm={12}>
                                    <FormControl
                                        fullWidth
                                        error={Boolean(errors && errors.resume)}
                                        sx={{ ...theme.typography.customInput }}
                                    >
                                        <TextField
                                            fullWidth
                                            required
                                            label="Resumo"
                                            margin="normal"
                                            name="resume"
                                            type="text"
                                            value={currentCampaign?.resume}
                                            onChange={(e) => setValue('resume', e.target.value)}
                                            inputProps={{ maxLength: 100 }}
                                            sx={{ ...theme.typography.customInput }}
                                        />
                                        {errors && errors.resume && (
                                            <FormHelperText error id="standard-weight-helper-text-email-login">
                                                {errors.resume}
                                            </FormHelperText>
                                        )}
                                    </FormControl>
                                </Grid>
                            </Grid>
                            {/* ========================================= */}
                            <Grid container spacing={matchDownSM ? 0 : 2}>
                                <Grid item xs={12} sm={12}>
                                    <TextEditorRender
                                        textValue={textEditor}
                                        loadBody={(editor) => loadBody(editor)}
                                        setTextEditor={(value) => setTextEditor(value)}
                                    />
                                </Grid>
                            </Grid>
                            <Grid container spacing={matchDownSM ? 0 : 2} sx={{ mt: 2 }}>
                                <Grid item xs={12} sm={12}>
                                    <Divider sx={{ flexGrow: 1 }} orientation="horizontal" />
                                </Grid>
                                <Grid item xs={12} sm={12} textAlign="center" alignItems="center" justifyContent="center">
                                    <strong>Imagem de capa</strong>
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <Divider sx={{ flexGrow: 1 }} orientation="horizontal" />
                                </Grid>
                                <Grid item xs={12} sm={12} textAlign="center" alignItems="center" justifyContent="center">
                                    {renderImageUpload()}
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <Divider sx={{ flexGrow: 1 }} orientation="horizontal" />
                                </Grid>
                            </Grid>
                            <Box sx={{ mt: 2 }}>
                                <AnimateButton>
                                    <Button
                                        disableElevation
                                        disabled={isSubmitting}
                                        fullWidth
                                        size="large"
                                        type="submit"
                                        variant="contained"
                                        color="secondary"
                                    >
                                        Salvar
                                    </Button>
                                </AnimateButton>
                            </Box>
                        </form>
                    )}
                </Formik>
            </>
        );
    }

    return (
        <Box xs={12} sm={12} sx={{ bgcolor: 'white', borderColor: 'divider' }}>
            <TabContext value={tab}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <TabList
                        textColor="secondary"
                        indicatorColor="secondary"
                        onChange={handleChange}
                        variant="scrollable"
                        scrollButtons="auto"
                        aria-label="Tabs Campaign"
                    >
                        <Tab label="Detalhes" value="1" />
                    </TabList>
                </Box>
                <TabPanel value="1">
                    <>
                        <span>Os campos marcados com ( * ) so de preenchimento obrigatrios</span>
                        <br />
                        {renderForm()}
                    </>
                </TabPanel>
            </TabContext>
        </Box>
    );
}

Manager.propTypes = {
    theme: PropTypes.shape({
        typography: PropTypes.shape({
            customInput: PropTypes.string
        })
    }),
    currentCampaign: PropTypes.shape({
        title: PropTypes.string.isRequired,
        resume: PropTypes.string.isRequired,
        startDate: PropTypes.string.isRequired,
        endDate: PropTypes.string,
        linkHtml: PropTypes.string.isRequired,
        linkImageEmphasis: PropTypes.string,
        multimedias: PropTypes.arrayOf(
            PropTypes.shape({
                imageBase64: PropTypes.string,
                linkMedia: PropTypes.string
            })
        )
    })
};

export default Manager;
